<template>
    <div class="cy_win">
        <div id="test"></div>
        <div class="line"></div>

<div class="codeArea">
<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;div id="test"&gt;&lt;/div&gt;
&lt;script type="text/javascript"&gt;
    import adChModule from "cy_static/adChModule";
    adChModule("#test", {
        "keyId": "Mall_Welfare_adverts",
        "column": "47.4",
        "unLazyNum": "3",
        "rowSpace": "1",
        "colSpace": "1",
        "paddingTop": "0",
        "paddingBottom": "0",
        "paddingLeft": "0",
        "paddingRight": "0",
        "backgroundColor": "#f0f0f0"
    });
&lt;/script&gt;
</xmp >
</div>

        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="line"></div>
        <div class="cy_example">
            <iframe ref="iframe" src="?t=2#/runcode"></iframe>
        </div>
    </div>
</template>

<script>
    import {trim} from "../../../assets/js/codeStyle";
    import adChModule from "cy_static/adChModule";
    export default {
        name: 'adChModule ',
        data () {
            return {
                msg: 'adChModule ',
                cmOptions:{
                        mode : "text/html",  // 模式
                        indentUnit : 2,  // 缩进单位，默认2
                        smartIndent : true,  // 是否智能缩进
                        tabSize : 4,  // Tab缩进，默认4
                        showCursorWhenSelecting : true

                },
                cmOptions1:{
                        readOnly:"true",
                        theme : "darcula",
                        mode : "text/html",  // 模式
                        indentUnit : 2,  // 缩进单位，默认2
                        smartIndent : true,  // 是否智能缩进
                        tabSize : 4,  // Tab缩进，默认4
                        showCursorWhenSelecting : true
                }
            }
        },
        mounted(){
            var m = this;  
            ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
                var dom, valDom, index = 0, getDom = function() {
                    index++;
                    dom = m.$refs[element + index];
                    valDom = m.$refs[element + "Val" + index];
                };
                getDom();
                while (dom) {
                    dom.codemirror.setValue(trim(valDom.innerHTML));
                    getDom();
                }
            });
            adChModule("#test", {
                data : [{"adContent":"https://image.changyoyo.com/uploadimage/IMG20190729184643595.jpg","adDesc":"1","adName":"1元包邮","adUrl":"https://p.pinduoduo.com/rptXjED6","adWord":"1元包邮","id":"P000004117","positionName":"福利社活动区","sort":10,"usePlat":"APP"},{"adContent":"https://image.changyoyo.com/uploadimage/IMG20190404102427729.jpg","adDesc":"2","adName":"抢红包","adUrl":"https://p.pinduoduo.com/2YlXnX8v","adWord":"抢红包","id":"P000004118","positionName":"福利社活动区","sort":3,"usePlat":"APP"},{"adContent":"https://image.changyoyo.com/uploadimage/IMG2019040410243677.jpg","adDesc":"3","adName":"大转盘","adUrl":"https://p.pinduoduo.com/WC2Xh8CK","adWord":"大转盘","id":"P000004119","positionName":"福利社活动区","sort":2,"usePlat":"APP"}],
                "keyId": "Mall_Welfare_adverts",
                "column": "47.4",
                "unLazyNum": "3",
                "rowSpace": "1",
                "colSpace": "1",
                "paddingTop": "0",
                "paddingBottom": "0",
                "paddingLeft": "0",
                "paddingRight": "0",
                "backgroundColor": "#f0f0f0"
            });
        }, 
        methods:{
            runcode(){
                var m = this, frame = m.$refs.iframe;
                frame.src = "?a=" + Math.random() + "#/runcode"
                frame.onload = function() {
                    frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
                }

            }
        }
    }
</script>

<style scoped>    
    .cy_win{display:flex;flex-flow:column;flex:1}
    .codeArea{height:310px;overflow: hidden;}
    .cy_example{position:static;flex:10}  
</style>


